const initDatabase = function(){
  let db
  let openRequest
  const dbName = this.$store.state.username
  const tableName = this.$store.state.username
  const collection = this.$store.state.username + 'collection'
  const reply = this.$store.state.username + 'reply'
  const comments = this.$store.state.username + 'comments'
  const replyToAuthor = this.$store.state.username + 'replytoauthor'
  const indexedDB = window.indexedDB || window.webkitIndexedDB || window.mozIndexedDB || window.msIndexedDB
  if (!indexedDB) {
    alert("IndexedDB")
  }
  return new Promise(resolve => {
    openRequest = indexedDB.open(dbName, 3) //

    //  
    openRequest.onupgradeneeded = function (event) {
      // console.log('')
      db = event.target.result
      // console.log(db.version, '')

      if (!db.objectStoreNames.contains(tableName)) { //
        let objectstore = db.createObjectStore(tableName, {keyPath: 'id', autoIncrement: true}) // id, 
        //IDBObject.createIndex()
        objectstore.createIndex('title', 'title', {unique: false})
      }

      if (!db.objectStoreNames.contains(collection)) { //
        let objectstore = db.createObjectStore(collection, {keyPath: 'id', autoIncrement: true}) // id, 
        //IDBObject.createIndex()
        objectstore.createIndex('title', 'title', {unique: false})
      }

      if (!db.objectStoreNames.contains(reply)) { //
        let objectstore = db.createObjectStore(reply, {keyPath: 'id', autoIncrement: true}) // id, 
        //IDBObject.createIndex()
        objectstore.createIndex('title', 'title', {unique: false})
      }

      if (!db.objectStoreNames.contains(comments)) { //
        let objectstore = db.createObjectStore(comments, {keyPath: 'id', autoIncrement: true}) // id, 
        //IDBObject.createIndex()
        objectstore.createIndex('title', 'title', {unique: false})
      }

      if (!db.objectStoreNames.contains(replyToAuthor)) { //
        let objectstore = db.createObjectStore(replyToAuthor, {keyPath: 'id', autoIncrement: true}) // id, 
      }

    }

    //()
    openRequest.onsuccess = function (event) {
      db = event.target.result
      console.log(666, 'db', db.version)
      db.onerror = function (event) {
        //
        alert("Database error:" + event.target.errorCode)
        console.dir(event.target)
      }
      resolve(db)
    }

    //
    openRequest.onerror = function (event) {
      console.log(event, '')
    }
  })
}

export {initDatabase}
